.sign-up {
  display: flex;
  flex-direction: column;

  min-height: 100%;

  &__main {
    flex: 1 1 auto;

    background-image: url('/assets/img/login-bg.jpg');
    background-repeat: no-repeat;
    background-position-y: 80%;
    background-size: 100%;

    @media (max-width: 1280px) {
      background-position: center;
      background-size: cover;
    }
  }

  &__inner {
    margin-bottom: 1px;
    padding: 19px 20px;
    padding-bottom: 18px;
  }

  &__form {
    max-width: 380px;
    margin: 0 auto;
  }
}
